.App {
  text-align: center;
  position: relative;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-float infinite 3s ease-in-out;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: rgb(112, 76, 182);
}

@keyframes App-logo-float {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(0px);
  }
}
.main {
  padding: 20px;
  padding-left: 250px;
}
.flex {
  display: flex;
}
.dashboard-form-container {
  width: 100%;
  margin: 0;
  text-align: left;
  padding: 10px 30px;
}

.dashboard-form-container .div {
  max-width: 400px;
}

.dashboard-form-container h2 {
  font-size: 1.5rem;
  margin-bottom: 10px;
}
